 body{
     margin: 0;
     
        background-image: linear-gradient(45deg, transparent 20%, black 25%, transparent 25%), 
                          linear-gradient(-45deg, transparent 20%, black 25%, transparent 25%), 
                          linear-gradient(-45deg, transparent 75%, black 80%, transparent 0),
                          radial-gradient(rgb(25, 45, 75) 2px, transparent 0) ;
        background-size: 30px 30px, 30px 30px;
        background-attachment: fixed;
    
 }
 .main{
    margin: 5%;
    /* border: 1px solid; */
    border-radius: 10px;
    height: 90vh;
    box-shadow:5px 5px 5px rgba(29, 43, 83,0.3) ;
    background: white; 
 }

 input{
     margin: 20px;
     border-radius: 5px;
 }

 .top{
    border-radius: 5px !important;
    background: linear-gradient(to left, rgba(7,27,82,1)5%, rgba(0,128,128,1)100%) !important;
 }